//  /  
var spinner = $('.ymap-container').children('.loader');
//        . (     )
var check_if_load = false;

//            "map-yandex"
function init() {
    var elements1 = document.querySelectorAll('.lobnenskaya');
    var elements2 = document.querySelectorAll('.kalugskaya');
    var elements3 = document.querySelectorAll('.sevastopolskiy');


    var myMapTemp = new ymaps.Map("map-yandex", {
        center: [55.730138, 37.594238], //    
        zoom: 10, //   
        controls: ['zoomControl', 'fullscreenControl'] //    ,    
    });    

    var myPlacemark  = new ymaps.Placemark([55.892144, 37.524160], {balloonContent: '<strong>, 17 .5</strong><br><br><center class="popup-call text-block__reviews-link">+7 (499) 490-27-73</center><br>', }, {iconLayout: 'default#image', iconImageSize: [40, 40], iconImageHref: '/img/map-baloon.png'});
    var myPlacemark2 = new ymaps.Placemark([55.655718, 37.553078], {balloonContent: '<strong>  . 14 . 7</strong><br><br><center class="popup-call text-block__reviews-link">+7 (495) 477-33-96</center><br>', }, {iconLayout: 'default#image', iconImageSize: [40, 40], iconImageHref: '/img/map-baloon.png'});
    var myPlacemark3 = new ymaps.Placemark([55.635345, 37.543578], {balloonContent: '<strong> -, 95 .3</strong><br><br><center class="popup-call text-block__reviews-link">+7 (499) 444-14-37</center><br>', }, {iconLayout: 'default#image', iconImageSize: [40, 40], iconImageHref: '/img/map-baloon.png'});

    if(!elements1.length == 0) {
        myMapTemp.geoObjects.add(myPlacemark); //    
    }
    if(!elements2.length == 0) {
        if (!$('#map-yandex').data('detailing')) {
            myMapTemp.geoObjects.add(myPlacemark2); //    
        }
    }
    if(!elements3.length == 0) {
        myMapTemp.geoObjects.add(myPlacemark3); //    
    }
    //     ,    
    var layer = myMapTemp.layers.get(0).get(0);

    //   callback-     
    waitForTilesLoad(layer).then(function () {
        //       
        spinner.removeClass('is-active');
    });
}

//       (     ) 
function waitForTilesLoad(layer) {
    return new ymaps.vow.Promise(function (resolve, reject) {
        var tc = getTileContainer(layer), readyAll = true;
        tc.tiles.each(function (tile, number) {
            if (!tile.isReady()) {
                readyAll = false;
            }
        });
        if (readyAll) {
            resolve();
        } else {
            tc.events.once("ready", function () {
                resolve();
            });
        }
    });
}

function getTileContainer(layer) {
    for (var k in layer) {
        if (layer.hasOwnProperty(k)) {
            if (
                    layer[k] instanceof ymaps.layer.tileContainer.CanvasContainer
                    || layer[k] instanceof ymaps.layer.tileContainer.DomContainer
                    ) {
                return layer[k];
            }
        }
    }
    return null;
}

//   API .   (    )
function loadScript(url, callback) {
    var script = document.createElement("script");

    if (script.readyState) {  // IE
        script.onreadystatechange = function () {
            if (script.readyState == "loaded" ||
                    script.readyState == "complete") {
                script.onreadystatechange = null;
                callback();
            }
        };
    } else {  //  
        script.onload = function () {
            callback();
        };
    }

    script.src = url;
    document.getElementsByTagName("head")[0].appendChild(script);
}

//  ,          "ymap-container"
var ymap = function () {
    $('.ymap-container').mouseenter(function () {
        if (!check_if_load) { //      .,  ,  

            //      ,    
            check_if_load = true;

            //      ,    
            spinner.addClass('is-active');

            //  API .
            loadScript("https://api-maps.yandex.ru/2.1/?lang=ru_RU&amp;loadByRequire=1", function () {
                //   API . ,          "map-yandex"
                ymaps.load(init);
            });
        }
    }
    );
}

$(function () {

    //  
    ymap();
    console.log('yamap works');
});